.batman {
    height: 170px;
    width: 170px;
	margin: 25px 72px;
	-webkit-box-shadow: 4px 3px 11px #000;
	        box-shadow: 4px 3px 11px #000;
	border-radius: 5px;
}

.batman:hover {
	cursor: pointer;
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
}

@media only screen and (max-width: 640px) {
	.batman {
	    height: 100px;
	    width: 100px;
	    margin: 5px 2px;
	}
}


