nav {
	background: #e8e820;
	padding: 30px;
	position: fixed;
	z-index: 99;
	width: 100%;
	-webkit-box-shadow: 
	0 14px 28px rgba(0,0,0,.25), 
	0 10px 10px rgba(0,0,0,.22);
	        box-shadow: 
	0 14px 28px rgba(0,0,0,.25), 
	0 10px 10px rgba(0,0,0,.22);
}

li {
	display: inline-block;
	text-align: center;
	width: 33.3%;
}

.nav-title {
	font-size: 20px;
}

.nav-message, .scores {
	font-size: 30px;
}

@media only screen and (max-width: 1285px) {

	li {
		width: 28.3%;
	}
}

@media only screen and (max-width: 640px) {

	li {
		width: 28.3%;
	}

	.nav-message, .scores , .nav-title {
		font-size: 16px;
		
	}
}




